

@font-face {
    font-family: 'Cubic';
    src: url('../../../../../../../public/Cubic_11_1.010_R.ttf') format('truetype');

}


.user-message {
    display: flex;
    flex-direction: row-reverse;

    border: 1px solid rgba(220, 209, 13, 0.879);
    border-radius: 8px;
    margin: 10px 0;
    font-size: 24px;
    font-family: 'Cubic';
    color: aliceblue;
    background-color: rgba(245, 210, 179,0.7);
    
}

.response-message{
    display: flex;
    flex-direction: row-reverse;
    border: 1px solid rgba(36, 28, 143, 0.879);
    border-radius: 8px;
    margin: 10px 0;
    font-size: 24px;
    font-family: 'Cubic';

    color: aliceblue;
    background-color: rgba(135, 168, 235,0.7);
    
}

.inputText{
    font-size: 24px;
    width: 400px;
    height: 50px;
    margin : 30px 20px;
    border: 1px solid rgba(255, 255, 255, 0);
    border-radius: 10px;
    font-family: 'Cubic';

}
