.pageContent {
    display: flex;
    height: calc(100vh - 70px );

}


@media screen and (max-width:0  ) {
    .side-nav {        
        height: 300px;
        margin: auto;
        display: flex;
    }
}

@media screen and (min-width:800px )and (max-width:960px)  {
    .side-nav {
        width: 800px;
        height: 300px;
        margin: auto;
        display: flex;
    }
    

}
@media screen and (min-width:960px  ) {
    .side-nav {
        width: 1000px;
        height: 300px;
        margin: auto;
        display: flex;
    }
    
}

.side-nav-item li {
    display: flex;
    position: relative;
    z-index: 1;
    min-width: 100px;
    background: gray;
    border: 2px solid rgb(128, 32, 218);
    border-radius: 4px;
    color: black;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 800;
    text-align: center;
    text-decoration: none;
    overflow: hidden;
    transition: 0.5s;
    padding: 10px 20px;
    opacity: 0.75;
    margin-bottom: 5px;
}

.side-nav-item li span {
    position: absolute;
    width: 25%;
    height: 100%;
    background-color: rgb(32, 137, 218);
    transform: translateY(150%);
    border-radius: 50%;
    left: calc((var(--n) - 1) * 25%);
    transition: 0.5s;
    transition-delay: calc((var(--n) - 1) * 0.1s);
    z-index: -1;
}
.side-nav-item li:hover,
.side-nav-item li:focus {
    color: black;
    opacity: 0.9;
}
.side-nav-item li:hover span {
    transform: translateY(0) scale(2);
}
.side-nav-item li span:nth-child(1) {
    --n: 1;
}
.side-nav-item li span:nth-child(2) {
    --n: 2;
}
.side-nav-item li span:nth-child(3) {
    --n: 3;
}
.side-nav-item li span:nth-child(4) {
    --n: 4;
}

.showTable {
    width: 1000px;
    height: 580px;
    background-image: url('../../../../public/images/raincoffee.gif');
    background-repeat: no-repeat;
    box-shadow:0 0 5px 3px rgb(83, 171, 206);
    overflow: scroll;

}



.list-group-item {
    text-shadow: 0 0 20px black;
    font-size: 20px;
    color: whitesmoke;
    opacity: 1;
    text-decoration: none;

}

