.routeList {
    width: 600px;
    height: 600px;
    box-sizing: border-box;
}

.routeHead {

    width: 850px;
    height: 50px;
    background-color: antiquewhite;
    display: flex;
    text-transform: uppercase;
    justify-content: space-around;
}

.routeHead li {

    width: 200px;
    height: 50px;
    display: flex;
    border: 1px solid black;
    text-align: center;
    line-height: 50px;
    font-size: 30px;
    font-weight: bold;
}

.routeHead li,
.routeHead li:focus {
    position: relative;
    min-width: 200px;
    border: none;
    border-radius: 4px;
    background-color: #16a085;
    color: #ffffff;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    transition-duration: 0.4s;
    padding: 0 20px;
    box-shadow: 0 5px 15px #193047;
}

.routeHead li:hover {
    background: #ffffff;
    box-shadow: 0px 2px 10px 5px #1abc9c;
    color: #000000;
}

.routeHead li:after {
    content: "";
    display: block;
    position: absolute;
    background: #1abc9c;
    padding-top: 300%;
    padding-left: 350%;
    margin-left: -20px;
    margin-top: -120%;
    opacity: 0;
    transition: all 0.8s
}

.routeHead li:hover:after {
    padding: 0;
    margin: 0;
    opacity: 1;
    transition: 0s;
}


.newsBlock {
    margin: 0 auto;
    text-decoration: none;

}

.msgBlock {
    margin: 0 auto;
    text-decoration: none;

}



.titleTable {
    font-size: 20px;
    font-weight: bolder;
    text-decoration: none;

    background-color: gray;
    margin-top: 40px;
    animation: fadeInAnimation ease 2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}


@keyframes fadeInAnimation { 
    0% { 
        opacity: 0; /*设置不透明度*/
    } 
    100% { 
        opacity: 1; 
    } 
} 

.newsTitleItem{
    text-decoration: none;
}

.dataTable {
    display: flex;
    margin-top: 50px;
    color: azure;
    flex-direction: row;


}

.newsImage {
    width: 290px;
    display: flex;
    flex-direction: row;
    animation: fadeInAnimation ease 2s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.newsTextBlock{
    
 font-weight: bold;
    
 display: flex;
 flex-direction: column;
 animation: fadeInAnimation ease 2s;
 animation-iteration-count: 1;
 animation-fill-mode: forwards;
 
}
