.weatherStyle{
    height: 405px;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.base{
    margin:0px auto;
    padding: 20px;
    width: 450px;
    background-color: rgba(123, 93, 17, 0.7);
    border-radius: 20px;
    box-shadow: 0,20px,30px rgba(0, 0, 0, 2);
    border: 1px solid whitesmoke
}
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@700;900&display=swap');

@font-face {
    font-family: 'Cubic';
    src: url('../../../../../../public/Cubic_11_1.010_R.ttf') format('truetype');

}

h1{
    font: 900 40px/1.4 "Noto Sans TC";
    color: rgba(101, 212, 216, 0.7);
    text-shadow: 0.1em 0.1em 0.2em black;
    font-family: 'Cubic';


}
.choceLocation{
    font-size: 20px;
    display: block;
    width: 100%;
    border: none;
    background: #eee;
    padding: 10px;
    border-radius: 99em;
}
.cityName{
    font: 500 40px/1.9 "Noto Sans TC";
    color: darkcyan;
    text-align: center;
    letter-spacing: 2px;
    text-shadow: 0.1em 0.1em 0.2em black;
    font-family: 'Cubic';


}
.datatext{
    color: aliceblue;
    font-family: 'Cubic';
    font-size: 25px;

}